<template>
  <div class="bmi">
    <div class="bmi-wrapper">
      <div class="title">BMI CALCULATOR</div>
      <div class="content-text">
        <p>
          Body Mass Index (BMI) is a common way to determine your health risk,
          and is calculated using your weight and height. BMI categorizes your
          body into four weight classes of underweight, normal, overweight, and
          obese. You can calculate your BMI to determine if you are underweight,
          overweight, normal, or obese. With the BMI calculator you can also see
          your risk of developing diseases such as heart disease, diabetes,
          cancer, and osteoporosis.
        </p>
        <br>
        <p>Knowing BMI can help adult men and women understand their overall health. Use the BMI calculator below to determine your body mass index by inputting your height and weight.</p>
      </div>

      <div class="calculator-wrapper">
            <Row>
                <Col span="14" style="padding:20px;background: #f9f9f9;">
                    <Form ref="formInline" :model="formInline" :rules="ruleInline">
                        <FormItem prop="weight">
                            <Input type="text" v-model="formInline.weight" placeholder="Your weight...(kgs)" number>
                            </Input>
                        </FormItem>
                        <FormItem prop="height">
                            <Input type="text" v-model="formInline.height" placeholder="Your height...(cm)" number>
                            </Input>
                        </FormItem>
                        <FormItem style="margin-bottom:0px">
                            <div style="display:flex;justify-content:space-between;">
                            <Button type="error" size="large" @click="handleSubmit('formInline')">Calculate</Button>
                            <!-- <div>Metric
                                <i-switch  true-color="#383838" false-color="#383838" v-model="formInline.switch" size="small">
                                    <span slot="open"></span>
                                    <span slot="close"></span>
                                </i-switch>
                                Imperial
                            </div> -->
                            </div>

                        </FormItem>
                    </Form>
                </Col>
                <Col span="10" class="result-box" style="padding:20px;background: #f1f1f1;">
                    <div class="result-title">YOUR BODY MASS INDEX IS</div>
                    <div class="result">
                        <img v-if="!result" src="@/assets/question.png" alt="">
                        <div v-else>
                            <h1>
                                {{ result }}
                            </h1>
                        </div>
                    </div>
                    <div class="result-text">
                        These calculations are based on averages.
                    </div>
                </Col>
            </Row>
      </div>

      <div class="title">UNDERSTANDING YOUR BODY MASS INDEX</div>
      <div class="content-text">
        <p>
            Now that you know your body mass index you are one step closer to mastering your overall health. Find out how to understand your BMI.
        </p>
        <br>
        <p><b>If your BMI is below 18.5:</b></p>
        <br>
        <p>Your BMI is considered underweight. Keep in mind that an underweight BMI calculation may pose certain health risks. Please consult your healthcare provider for more information about BMI calculations.</p>
        <br>
        <p><b>If your BMI is between 18.5 – 24.9:</b></p>
        <br>
        <p>Your BMI is considered normal. This healthy weight helps reduce your risk of serious health conditions and means you’re close to your fitness goals.</p>
        <br>
        <p><b>If your BMI is above 30:</b></p>
        <br>
        <p>Your BMI is considered overweight. Being overweight may increase your risk of cardiovascular disease. Consider making lifestyle changes through healthy eating and fitness to improve your health.</p>
      </div>
    </div>
  </div>
</template>

<script>
    export default {
        data () {
           const validateInput = (rule, value, callback) => {
                if (!value) {
                    return callback(new Error('Fields cannot be empty'));
                }
                // 模拟异步验证效果
                setTimeout(() => {
                    if (!Number.isInteger(value)) {
                        callback(new Error('Please enter a numeric value'));
                    } else {
                        callback();
                    }
                }, 1000);
            };

            return {
                result: '',
                formInline: {
                    switch:'',
                    height: '',
                    weight: ''
                },
                ruleInline: {
                    weight: [
                        { required: true, validator: validateInput, trigger: 'submit' }
                    ],
                    height: [
                        { required: true, validator: validateInput, trigger: 'submit' },
                    ]
                }
            }
        },
        methods: {
            handleSubmit(name) {
                this.$refs[name].validate((valid) => {
                    if (valid) {
                        var weight = this.formInline.weight;
                        var height = this.formInline.height;
                        let bmi = (weight / ((height * height) / 10000)).toFixed(2);
                        this.result = bmi;
                        console.log(bmi);
                    } else {
                    }
                })
            }
        }
    }
</script>

<style>
.result-title {
    font-weight: 600;
}
.result-box {
    text-align: center;
}

.result-box .result img {
    width: 50px;
    height: 50px;
    margin: 20px auto;
}

.result-box .result div {
    font-weight: 600;
    font-size: 25px;
    color: #000;
}

.result-box .result-text {
    font-size: 12px;
}
.calculator-wrapper {
    margin: 40px 0px;
}
.bmi {
    padding-right: 50px;
}
.title {
    font-weight: 600;
    font-size: 18px;
    margin-bottom: 20px;
}

.content-text {
    font-size: 14px;
}
</style>
